.dance {
	margin: 0;
	padding: 0;
}
.dance p {
	padding: 1vh 20% 2vh 20%;
	text-align: justify;
	font-size: 0.8em;
}
@media screen and (max-width: 1400px) {
	.dance p {
		padding: 0 10% 1vh 10%;
	}
}
@media screen and (max-width: 1000px) {
	.dance p {
		font-size: 0.6em;
	}
}
@media screen and (max-width: 700px) {
	.dance p {
		padding: 0 5% 1vh 5%;
	}
}
